import EOS from "eosjs";
import BigNumber from "bignumber.js";
import _compact from "lodash/compact";
import _filter from "lodash/filter";
import { UPDATE_ROUND_INFO, UPDATE_TOKEN_INFO, UPDATE_PLAYER_INFO, UPDATE_AFFILIATE_INFO } from "./index";
import { toggleNetwork, getReadOnlyEOS, getEOSClient } from "../networks/index";

export const UPDATE_MODAL = Symbol('UPDATE_MODAL');

// 校对时间
export function updateModal(payload) {
  return (dispatch, getState) => {
    // 模拟获取服务器时间
    dispatch({
      type: UPDATE_MODAL,
      payload
    })
  }
}
